.container {
    width: 100%;
    margin: auto;
    padding: 50px 100px 100px 100px;

    display: flex;
    justify-content: space-between;
}

.box {
    width: max-content;
    text-align: center;
}

.heading {
    color: var(--skyblue);
    font-size: 40px;
    font-weight: 600;
    padding: 20px 0;
    /* line-height: 44px; */
}

.sub_heading {
    color: var(--grey);
    font-weight: 400;
    letter-spacing: 2px;
    font-size: 17px;
    text-transform: uppercase;
}



@media (max-width: 600px) {
    .container {
        width: 100%;
        /* border: 1px solid red; */
        padding: 30px 0;
        display: flex;
        flex-direction: column;
    }

    .box {
        width: 100%;
        padding: 20px 0;
    }
    
    .heading {
        color: var(--skyblue);
        font-size: 32px;
        font-weight: 600;
        padding: 20px 0;
    }

    .sub_heading {
        color: var(--grey);
        font-weight: 400;
        letter-spacing: 2px;
        font-size: 14px;
        text-transform: uppercase;
    }

}